  // box-shadow: inset 0 0 1px #FFF, inset 0 1px 7px #DDD, 0 3px 6px -3px #BBB;
// }

.icomoon-noUi-startIcon:before {
  font-family: icomoon !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  line-height: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.5625em;
  background-color: white;
  color: green;
  border-radius: 1.5625em;
  margin-left: -100%;
  position: relative;
  top: -0.665em;
  // display: block;
  // position: absolute;
  // height: 14px;
  // width: 14px;
  // top: 6px;
}

.icomoon-noUi-endIcon:before {
  font-family: icomoon !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  line-height: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.5625em;
  background-color: white;
  color: green;
  border-radius: 1.5625em;
  margin-left: 97.5%;
  position: relative;
  top: -1.5em;
  // display: block;
  // position: absolute;
  // height: 14px;
  // width: 14px;
  // top: 6px;
}


/* Handle stripes;
 */

:host /deep/ .noUi-handle:before {
  font-family: icomoon !important;
  font-style: normal !important;
  font-weight: normal !important;
  font-variant: normal !important;
  text-transform: none !important;
  line-height: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  // content: '\\ea7f';
  // content: var(--middleIcon);
  font-size: 2.187em;
  background-color: white;
  border-radius: 2.187em;
  margin-left: -0.5em;
  cursor: pointer;
  color: #258900;
  // display: block;
  // position: absolute;
  // height: 14px;
  // width: 14px;
  // top: 6px;
}

// .noUi-handle:before,
// .noUi-handle:after {
//   display: block;
//   position: absolute;
//   height: 14px;
//   width: 1px;
//   background: #E8E7E6;
//   left: 14px;
//   top: 6px;
// }
:host /deep/ .noUi-handle:after {
  left: 17px;
}

:host /deep/ .noUi-vertical .noUi-handle:before, .noUi-vertical .noUi-handle:after {
  width: 14px;
  height: 1px;
  left: 6px;
  top: 14px;
}

:host /deep/ .noUi-vertical .noUi-handle:after {
  top: 17px;
}


/* Di:host /deep/ sabled state;
 */

:host /deep/ [disabled] .noUi-connect {
  background: #B8B8B8;
}

:host /deep/ [disabled].noUi-target, [disabled].noUi-handle, [disabled] .noUi-handle {
  cursor: not-allowed;
}


/* Base;
 *
 */

:host /deep/ .noUi-pips, .noUi-pips * {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

:host /deep/ .noUi-pips {
  position: absolute;
  color: #999;
}


/* Values;
 *
 */

:host /deep/ .noUi-value {
  position: absolute;
  white-space: nowrap;
  text-align: center;
  color: black
}

:host /deep/ .noUi-value-sub {
  color: #ccc;
  font-size: 10px;
  color: transparent;
}


/* Markings;
 *
 */

:host /deep/ .noUi-marker {
  position: absolute;
  background: #CCC;
}

:host /deep/ .noUi-marker-sub {
  background: #AAA;
}

:host /deep/ .noUi-marker-large {
  background: #AAA;
}


/* Horizontal layout;
 *
 */

:host /deep/ .noUi-pips-horizontal {
  padding: 10px 0;
  height: 80px;
  top: 100%;
  left: 0;
  width: 100%;
}

:host /deep/ .noUi-value-horizontal {
  -webkit-transform: translate(-50%, 50%);
  transform: translate(-50%, 50%);
}

:host /deep/ .noUi-rtl .noUi-value-horizontal {
  -webkit-transform: translate(50%, 50%);
  transform: translate(50%, 50%);
}

:host /deep/ .noUi-marker-horizontal.noUi-marker {
  margin-left: -1px;
  width: 2px;
  height: 5px;
  width: 0px;
}

:host /deep/ .noUi-marker-horizontal.noUi-marker-sub {
  height: 10px;
}

:host /deep/ .noUi-marker-horizontal.noUi-marker-large {
  height: 15px;
}


/* Vertical layout;
 *
 */

:host /deep/ .noUi-pips-vertical {
  padding: 0 10px;
  height: 100%;
  top: 0;
  left: 100%;
}

:host /deep/ .noUi-value-vertical {
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%, 0);
  padding-left: 1.5625em;
}

:host /deep/ .noUi-rtl .noUi-value-vertical {
  -webkit-transform: translate(0, 50%);
  transform: translate(0, 50%);
}

:host /deep/ .noUi-marker-vertical.noUi-marker {
  width: 5px;
  height: 2px;
  margin-top: -1px;
}

:host /deep/ .noUi-marker-vertical.noUi-marker-sub {
  width: 10px;
}

:host /deep/ .noUi-marker-vertical.noUi-marker-large {
  width: 15px;
}

:host /deep/ .noUi-tooltip {
  display: block;
  position: absolute;
  // border: 1px solid #D9D9D9;
  // border-radius: 3px;
  // background: #fff;
  // color: #000;
  padding: 0.312em;
  // text-align: center;
  white-space: nowrap;
  border-width: 0px;
  position: absolute;
  left: 0px;
  // top: 0px;
  width: 4.375em;
  height: 2.75em;
  background: inherit;
  background-color: rgba(111, 111, 111, 1);
  border: none;
  border-radius: 0px;
  box-shadow: none;
  font-family: 'Arial Bold', 'Arial';
  font-weight: 700;
  font-style: normal;
  font-size: 1em;
  color: #FFFFFF;
  text-align: center;
  line-height: 1.25em;
  padding-top: 0.75em;
  margin-left: -0.7em;
}

:host /deep/ .noUi-horizontal .noUi-tooltip {
  -webkit-transform: translate(-50%, 0);
  transform: translate(-50%, 0);
  left: 63%;
  bottom: 190%;
}

:host /deep/ .noUi-vertical .noUi-tooltip {
  -webkit-transform: translate(0, -50%);
  transform: translate(0, -50%);
  top: 50%;
  right: 120%;
}

.contrasto-on {
  :host /deep/ .noUi-target, .noUi-target * {
    -webkit-tap-highlight-color: @black !important;
  }
  :host /deep/ .noUi-target {
    background: @black !important;
  }
  :host /deep/ .noUi-connect {
    background: @black !important;
  }
  :host /deep/ .icomoon-noUi-startIcon:before {
    background-color: @white !important;
    color: @black;
  }
  :host /deep/ .icomoon-noUi-endIcon:before {
    background-color: @white !important;
    color: @black;
  }
  :host /deep/ .noUi-handle:before {
    background-color: @white !important;
    color: @black;
  }
  :host /deep/ [disabled] .noUi-connect {
    background: @black;
  }
  :host /deep/ .noUi-pips {
    color: @black;
  }
  :host /deep/ .noUi-value {
    color: @black
  }
  :host /deep/ .noUi-value-sub {
    color: transparent;
  }
  :host /deep/ .noUi-marker {
    background: @black;
  }
  :host /deep/ .noUi-marker-sub {
    background: @black;
  }
  :host /deep/ .noUi-marker-large {
    background: @black;
  }
  :host /deep/ .noUi-tooltip {
    background-color: @black;
    color: @white;
  }
}